form{
    display: grid;
    width: 50%;
    min-width: 300px;
    min-width: 600px;
    margin: 0 auto;
}
.success, .error{
  width: 400px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 10px;  
}
.success{
    background: green;
}
.error{
    background: red;
}

#contact{
    position: relative;
    width: 50%;
    margin: 60px auto;
    padding: 60px 30px;
    background: #c9d0de;
    border:1px solid #e1e1e1;
    box-shadow: 0px 0px 8px #444;
    background-image: url('../public/main_tel.png');
    background-repeat: no-repeat;
    background-size:contain;
    animation : ombre ease-in infinite alternate 500ms;
    
}

#contact h1
{
    font-size:35px;
    color:#445668;
    text-transform: uppercase;
    text-align: center;
    margin: 0 0 35px 0;
    text-shadow: 0px 1px 0px #f2f2f2;
}

#contact label{
    float: left;
    clear: left;
    margin: 11px 20px 0 0;
    width: 50%;
    text-align: right;
    font-size: 16px;
    color:#36485a;
    text-transform: uppercase;
    text-shadow: 0px 1px 0px #f2f2f2;
}

#contact input{
    width: 100%;
    height: 35px;
    padding: 5px 20px 0px 20px;
    margin: 0 0 20px 0;
    background: #a2adb8;
    border-radius: 5px;
    box-shadow: 0px 1px 0px #f2f2f2;
    font-family: sans-serif;
    font-size: 14px;
    color: #edd9d918;
    text-shadow: 0px -1px 0px #334f71;
}

#contact input:-webkit-input-placeholder{
    color:#f0f3f5;
    text-shadow: 0px -1px 0px #38506b;
}
#contact textarea{
    width: 50%;
    height: 170px;
    padding: 12px 20px 0 20px;
    margin:0 0 20px 0;
    background: #5E768D;
    border-radius: 5px;
    font-family: sans-serif;
    font-size: 14px;
    color:#f9f0f0;
    text-transform: uppercase;
    text-shadow: 0px -1px 0px #eef1f5;
}
#contact textarea::-webkit-input-placeholder{
    color:#a1b2c3;
    text-shadow: 0px -1px 0px #b6c5cf;
}

#contact input:focus, textarea:focus{
    background: #728eaa;

}

#contact input[type=submit]{
    width: 185px;
    height: 52px;
    float: right;
    padding: 10px 15px;
    margin:0 15px 0 0;
    box-shadow: 0px 0px 5px #999;
    border:1px solid #556f8c;
    cursor:pointer;
}
#contact #accord_donnees+label{
  color:rgb(249, 249, 251);
  font-size: clamp(12px,0.8rem,17px);
  text-align: left;
  margin-left: 25%;
}
